import React from 'react';
import PropTypes from 'prop-types';
import { NotesWrapper, BoxLabel, ComponentWrapper } from './Notes.style';

const Notes = ({ data }) => {
  const renderNotes = data?.map((item) => (
    <ComponentWrapper key={item.id}>
      <BoxLabel>{item.newDate}</BoxLabel>
      <NotesWrapper>
        <BoxLabel big>{item.body}</BoxLabel>
      </NotesWrapper>
    </ComponentWrapper>
  ));
  return <>{data?.length > 0 ? renderNotes : null}</>;
};

Notes.propTypes = {};

export default Notes;
